@import "./utils/_media-variables.scss";

// mobile
@media screen and (max-width: $mobile) {
	header{
			border-bottom: 1px solid var(--light-gray);

			.wrapper{
					display: flex;
					align-items: center;
					justify-content: space-between;
					position: relative;

					&:first-child{
							display: none;
					}

					&:last-child{
							padding: 20px 5px;
					}
			}

			hr{
				display: none;
			}

			.header-middle-list, .catalogue-open{
					display: none;
			}
	}

	input{
			&[type="text"],
			&[type="password"],
			&[type="tel"],
			&[type="email"]{
					padding-top: 7px;
					padding-bottom: 7px;
			} 
	}

	#burger-open{
			display: block;
	}

	.header_button-set{
			display: flex;

			a, button{
					display: none;
			}

			& #search-open,& #cart-open{
					display: block;
			}
	}

	#search-open, #cart-open, #burger-open{
			padding: 10px;

			svg{
					width: 15px;
					height: 15px;
					display: block;
			}
	}

	.header_middle{
			width: 100%;
			padding-left: 14px;
	}

	.header-middle_search{
			display: none;
			position: relative;
			padding-right: 10px;
	}

	.header-logo{
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			
			img{
					height: auto;
					width: 127px;
			}
	}

	.header_search-bar{
			border-bottom: 1px solid var(--light-gray);
			padding:7px 5px ;
			color: var(--gray);
			font:var(--body-2);
	}

	#header_search-bar-close{
			position: absolute;
			right: 10px;
			top: 50%;
			transform: translateY(-50%);
			
			svg{
					display: block;
					width: 20px;
					height: 20px;

					&:hover{
							path{
									fill: var(--gray);
							}
					}
			}
	}

	.header-middle-list{
			display: none;
	}

	.search-block{
			background: white;
			border-bottom: 1px solid var(--light-gray);
			padding-top: 35px;
			padding-bottom: 57px;
			position: absolute;
			left: 0;
			bottom: 0;
			width: 100%;
			height: calc(100vh - var(--header-height));
			overflow: auto;

			h2{
					margin-bottom: 20px;
			}

			.wrapper{
					max-width: 925px;
					padding: 0 55px;
			}

			li{
					margin-top: 10px;
			}
	}

	.search-block__results{
			margin-bottom: 30px;
	}

	// Auth
	#authorization{

			.wrapper{
					height: 100%;
					padding: 70px 15px;
					display: flex;
					justify-content: center;
					overflow: auto;
			}
	}

	.authorization-container{
			max-width: 320px;
			width: 100%;
			height: fit-content; 
height: -moz-fit-content;
			background: var(--white);
			padding: 40px 30px;
			padding-bottom: 40px;
			position: relative;

			h2{
					text-align: center;
					margin-bottom: 30px;
			}

			input[type="text"]{
					margin-bottom: 37px;
			}

			form{
					display: flex;
					flex-direction: column;
					height: fit-content; 
height: -moz-fit-content;
			}

			input[type="submit"]{
					padding: 20px;
					max-width: 270px;
					width: 100%;
					margin: 0 auto;
			}
	}

	#authorization-close{
			position: absolute;
			right: 10px;
			top: 10px;
			padding: 5px;
	}

	.password-input{
			position: relative;
	}

	.show-pass{
			position: absolute;
			right: 0;
			top: 50%;
			transform: translateY(-50%);
			padding: 5px;
			padding-right: 0;
			display: block;

			svg{
					display: block;
			}

			&.active{
					svg{
							path{
									fill: var(--black);
							}
					}
			}
	}

	.authorization__pass-forgot{
			text-align: right;
			display: inline-block;
			margin-top: 13px;
			margin-bottom: 37px;
	}

	// Cart
	#cart{

			.wrapper{
					width: 100%;
					overflow: auto;
			}
	}

	.cart-block{
			max-width: 720px;
			height: 100%;
			margin-left: auto;
			width: 100%;
			background: var(--white);
			display: flex;
			flex-direction: column;
			height: calc(100vh - var(--header-height));
	}

	.cart-main{
			padding: 20px 30px 24px 30px;
			display: flex;
			flex-direction: column;
			height: calc(100% - 200px);
	}

	.cart-header{
			display: flex;
			gap: 15px;
			justify-content: space-between;
			padding-bottom: 19px;
			border-bottom: 1px solid var(--light-gray);
	}

	#cart-close, .cart-item-remove{
			padding: 5px;
			margin-right: -5px;
			display: block;

			svg{
					display: block;
			}
	}

	.cart-lineitem{
			height: 100%;
			overflow: auto;
			flex-direction: column;
	}

	.cart-item{
			margin-top: 25px;
			overflow: hidden;
			padding-bottom: 15px;
			border-bottom: 1px solid var(--light-gray);
			width: 100%;
			height: fit-content; 
height: -moz-fit-content;
	}

	.cart-item__main{
			display: grid;
			grid-template-columns: min-content 1fr;
			grid-template-rows: min-content 1fr;
			column-gap: 40px;
			margin-bottom: 20px;

			h2{
					max-width: 310px;
			}
	}

	.cart-item__thumb{
			grid-column: 1/2;
			grid-row: 1/3;
			width: 120px;
			height: 120px;
			object-fit: cover;
	}

	.cart-item__header{
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 12px;
	}

	.cart-item-remove{
			display: block;
			height: fit-content; 
height: -moz-fit-content;
	}

	.cart-item__footer{
			display: flex;
			justify-content: space-between;
	}

	.cart-item__amount{
			display: flex;
			align-items: flex-end;
			gap: 50px;

			select{
					min-width: 64px;
					padding: 5px;
					border: 0;
					border-bottom: 1px solid var(--light-gray);
					color: var(--black);
					font: var(--h4);
					padding-left: 15px;
					-webkit-appearance: none;
					-moz-appearance: none;
					position: relative;
					background: transparent url("../img/select-arrow.svg") no-repeat right 4px center;
					background-position-y: 16px;
			}
	}

	.cart-summary{
			display: flex;
			align-items: flex-end;
			justify-content: space-between;
	}

	.cart-footer{
			display: flex;
			flex-direction: column;
			border-top: 1px solid var(--light-gray);

			a,button{
					padding: 36px;
			}
	}

	.cart-empty-footer{
			a{
					padding: 36px;
					display: block;
			}
	}

	.cart-empty{
			padding-top: 60px;

			h3{
					margin-bottom: 26px;
			}
	}

	// Favorites
	#favorites{
			.wrapper{
					width: 100%;
					overflow: auto;
			}
	}

	.favorites-block{
			max-width: 720px;
			height: 100%;
			margin-left: auto;
			width: 100%;
			background: var(--white);
			display: flex;
			flex-direction: column;
			height: calc(100vh - var(--header-height));
	}

	.favorites-main{
			padding: 20px 30px 24px 30px;
			display: flex;
			flex-direction: column;
			height: calc(100% - 100px);
	}

	.favorites-header{
			display: flex;
			gap: 15px;
			justify-content: space-between;
			padding-bottom: 19px;
			border-bottom: 1px solid var(--light-gray);
	}

	#favorites-close, .favorites-item-remove{
			padding: 5px;
			margin-right: -5px;
			display: block;

			svg{
					display: block;
			}
	}

	.favorites-lineitem{
			height: 100%;
			overflow: auto;
			flex-direction: column;
	}

	.favorites-item{
			margin-top: 25px;
			overflow: hidden;
			padding-bottom: 15px;
			border-bottom: 1px solid var(--light-gray);
			width: 100%;
			height: fit-content; 
height: -moz-fit-content;
	}

	.favorites-item__main{
			display: grid;
			grid-template-columns: min-content 1fr;
			grid-template-rows: min-content 1fr;
			column-gap: 40px;
			margin-bottom: 20px;

			h2{
					max-width: 310px;
			}
	}

	.favorites-item__thumb{
			grid-column: 1/2;
			grid-row: 1/3;
			width: 120px;
			height: 120px;
			object-fit: cover;
	}

	.favorites-item__header{
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 12px;
	}

	.favorites-item-remove{
			display: block;
			height: fit-content; 
height: -moz-fit-content;
	}

	.favorites-item__footer{
			display: flex;
			justify-content: space-between;
	}

	.favorites-item__amount{
			display: flex;
			align-items: flex-end;
			gap: 50px;

			select{
					min-width: 64px;
					padding: 5px;
					border: 0;
					border-bottom: 1px solid var(--light-gray);
					color: var(--black);
					font: var(--h4);
					padding-left: 15px;
					-webkit-appearance: none;
					-moz-appearance: none;
					position: relative;
					background: transparent url("../img/select-arrow.svg") no-repeat right 4px center;
					background-position-y: 16px;
			}
	}

	.favorites-summary{
			display: flex;
			align-items: flex-end;
			justify-content: space-between;
	}

	.favorites-footer{
			button{
					padding: 37px;
					display: block;
					width: 100%;
			}
	}


	.favorites-empty{
			padding-top: 60px;

			h3{
					margin-bottom: 26px;
			}
	}

	// catalogue
	#catalogue{
			
			.wrapper{
					width: 100%;
					overflow: auto;
					background: var(--white);
			}
	}

	.catalogue__heading{
			display: grid;
			grid-template-columns: 1fr;
			border-bottom: 1px solid var(--light-gray);

			a{
					padding: 21px 30px;
					display: block;
					text-align: center;
					border-top: 1px solid var(--light-gray);      

					&:hover{
							background: var(--light-gray);
					}
			}
	}

	.catalogue__main{
			padding: 42px 32px 120px 32px;
			
			&>ul{
					display: grid;
					grid-template-columns: 1fr;
					row-gap: 40px;
			}
	}

	.catalogue-item{
			display: flex;
			gap: 20px;

			a{
					&:hover{
							color: var(--gray);
					}
			}

			button{
					&:hover{
							color: var(--black);
					}
			}
	}

	.catalogue-item__thumb{
			width: 60px;
			height: 50px;
	}

	.catalogue-item__content{
			h2{
					min-height: 50px;
					margin-bottom: 25px;
			}

			ul{
					li{
							margin-bottom: 6px;
					}

					&.foldedList{
							margin-bottom: 9px;

							li{
									display: none;

									&:nth-child(1),
									&:nth-child(2),
									&:nth-child(3){
											display: block;
									}
							}
					}
			}
	}

	footer{
			.wrapper{
					&:nth-of-type(1){
							max-width: 310px;
							display: grid;
							grid-template-columns: 1fr 1fr;
							column-gap: 20px;
							row-gap: 40px;
					}
	
					&:nth-of-type(2){
							max-width: 310px;
							padding: 20px 10px;
							display: flex;
							flex-direction: column;
							gap: 0px;
					}
			}
	
			h2{
					margin-bottom: 15px;
			}
	
			ul{
					max-width: 150px;
					width: 100%;
			}
	
			li{
					margin-bottom: 3px;
	
					&:last-child{
							margin-bottom: 0;
					}
			}  
	}

	.hero{
			display: grid;
			grid-template-columns: calc(calc(150% * 100) / 360) calc(calc(120% * 100) / 360) 1fr;

			div{
					&:nth-of-type(1){
							grid-column: 2/4;
							display: flex;
							align-items: flex-end;
							padding: 24px 17px;
							min-height: 224px;
							border-bottom: 1px solid var(--light-gray);
					}
	
					&:nth-of-type(2){
							display: none;
					}
	
					&:nth-of-type(3){
							grid-column: 1/3;
							min-height: 295px;
							padding: 34px;
					}
	
					&:nth-of-type(4){
							border-left: 1px solid var(--light-gray);
					}
					
			}    

			.arrow-button{
					grid-column: 1/4;
					padding: 23px 46px;
	
					&::after{
							right: 16px;
					}
	
					&:hover{
							&::after{
									right: 10px;
							}
					}
			}
	}
	
	.hero__image{
			width: 100%;
			height: 100%;
			object-fit: cover;
	}

	.popular{

			&>div{   
					&:nth-of-type(1){
							padding: 28px 30px;
							border-bottom: 1px solid var(--light-gray);
					}
					
					a{
							border-bottom: 1px solid var(--light-gray);
							display: flex;
							flex-direction: row-reverse;
							justify-content: space-between;
							align-items: center; 
							gap: 20px;
							padding: 15px 36px 15px 30px;

							img{
									width: 70px;
									height: 70px;
									object-fit: contain;
							}
					}  
			} 
	}

	.popular__all .arrow-button{
			padding: 30px;;
			width: 100%;
			display: block;
			text-align: left;
	}

	.slider{
			width: 100%;
			display: flex;
			flex-direction: column;

			&>div{
					&:nth-of-type(1){
							border-bottom: 1px solid var(--gray);
							padding: 20px 30px;
							min-height: 115px;
							display: flex;
							align-items: flex-end;

							.swiper{
									height: 100%;
							}

							.swiper-slide{
									display: flex;
									align-items: flex-end;
							}

							.swiper-wrapper{
									height: 100%;
							}
					}

					&:nth-of-type(2){
							order: 4;
							border-right: 1px solid var(--gray);
							padding: 27px 33px;
							min-height: 233px;

							.swiper{
									height: 100%;
							}

							.swiper-slide{
									display: flex;
							}

							p{
									max-width: 285px;
							}
					}

					&:nth-of-type(3){
							.swiper-slide{

									img{
											width: 100%;
											height: 100%;
											border-bottom: 1px solid var(--gray);
									}
							}
					}
			}
	}

	.swiper-footer{
			border-bottom: 1px solid var(--gray);
			display: flex;
			justify-content: flex-end;
			align-items: center;
			margin-top: -5px;

			.arrow-button{
					padding: 21px 72px 21px 43px;
					flex-shrink: 0;

					&::after{
							right: 45px;
					}

					&:hover{
							&::after{
									right: 40px;
							}
					}
			}

			.pagination{
					text-align: left;
					padding-right: 10px;
					padding-left: 20px;
			}
	}

	.swiper-footer__navigation{
			height: 100%;
			border-left: 1px solid var(--gray);
			display: flex;
			gap: 15px;
			padding: 11px;

			.swiper-prev, .swiper-next{
					padding: 10px;
					cursor: pointer;
					svg{
							width: 15px;
							height: 15px;
							
					}
					
			}
	}

	.partners{
			display: grid;
			grid-template-columns: 1fr 1fr;

			div{
					&:nth-of-type(1){
							grid-column: 1/3;
							padding: 30px 40px;
							border-bottom: 1px solid var(--light-gray);
							display: flex;
							align-items: center;
							text-align: left;
							border-left: 1px solid var(--light-gray);
					}

					&:nth-of-type(2),
					&:nth-of-type(3),
					&:nth-of-type(4),
					&:nth-of-type(5),
					&:nth-of-type(6),
					&:nth-of-type(7){
							width: 100%;
							padding-top: 100%;
							position: relative;
							border-left: 1px solid var(--light-gray);
							border-bottom: 1px solid var(--light-gray);

							img{
									position: absolute;
									left: 50%;
									top: 50%;
									max-width: 140px;
									padding: 10px;
									width: 100%;
									height: auto;
									transform: translate(-50%,-50%);
							}
					}

					&:nth-of-type(2n+1){
							border-right: 1px solid var(--light-gray);
					}

					&:nth-of-type(8){
							grid-column: 1/3;
							border-left: 1px solid var(--light-gray);
							display: flex;
							justify-content: flex-end;
							align-items: center;
					}
			}

			.arrow-button{
					padding: 20px 15px;
					display: block;
					width: 100%;
					height: 100%;
					text-align: right;
			}
	}

	.contact-us{
			display: grid;
			grid-template-columns:1fr;

			&>div:first-child{
					padding: 30px 28px;
					border-bottom: 1px solid var(--gray);
			}
	}

	.contact-us__section{
			padding: 40px 34px;
			display: grid;
			grid-template-columns:1fr;
			gap: 20px;

			&:first-child{
					border-bottom: 1px solid var(--gray);
			}

			p:first-child{
					margin-bottom: 40px;
			}
	}

	.breadcrumbs{
		.wrapper{
			padding: 20px 16px;
		}

		a,ul{
			gap: 11px;
		}

		svg{
			width: 15px;
			height: 15px;
			margin-top: 2px;
		}
	}

	.catalogue-hero{
		.card{
			border-bottom: 1px solid var(--light-gray);

			a{
				padding: 45px 20px 10px 20px;
				gap: 40px;
			}

			img{
				width: 70px;
				height: 70px;
			}
		} 

		&>*:nth-child(2n-1){
			border-right: 1px solid var(--light-gray);
		}

		&>a{
			padding: 20px;
			font-size: 14px;
		}

		.arrow-button{

			span:after{
				display: none;
			}
		}
	}

	.catalogue-main{

		&>div{
			box-sizing: border-box;
			width: 100%;
			height: 100%;
			border-bottom: 1px solid var(--light-gray);		
		}
	}

	.catalogue-card{
		padding: 40px 30px 20px 30px;
	
		img{
			width: 70px;
			height: 70px;
			margin-bottom: 20px;
		}
	
		h2{
			padding-bottom: 10px;
		}
	
		ul{
			padding-top: 15px;
	
			li{
				margin-bottom: 3px;
			}
	
			&.foldedList{
				margin-bottom: 10px;
			}
		}
	}

	.catlogue-more{
		display: flex;
		justify-content: center;
		width: 100%;
		padding: 20px;

		svg{
			transform: rotate(90deg);
		}

		span{
			padding: 0;
			display: flex;
			align-items: center;
			gap: 15px;

			&:after{
				display: none;
			}
		}
	}

	.catalogue-category-hero{
		display: grid;
		grid-template-columns: calc(calc(150% * 100) / 360) 1fr;
		grid-template-rows: min-content min-content;

		div{
			border-bottom: 1px solid var(--light-gray);

			&:nth-of-type(1){
				border-right: 1px solid var(--light-gray);
				position: relative;
				width: 100%;
				padding-top: 120%;

				img{
					position: absolute;
					width: 100%;
					left: 0;
					top: 0;
					height: 100%;
					object-fit: cover;
				}
			}

			&:nth-of-type(2){
				padding: 20px;
				display: flex;
				align-items: flex-end;
			}

			&:nth-of-type(3){
				display: none;
			}

			&:nth-of-type(4){
				padding: 40px 35px;
				grid-column: 1/3;
				display: flex;
				align-items: center;
				justify-content: center;

			}

			&:nth-of-type(5){
				display: none;
			}
		}
	}

	.catalogue-brand-hero{
		display: grid;
		grid-template-columns: calc(calc(150% * 100) / 360) 1fr;
		grid-template-rows: min-content min-content;

		div{
			border-bottom: 1px solid var(--light-gray);

			&:nth-of-type(1){
				border-right: 1px solid var(--light-gray);
				position: relative;
				width: 100%;
				display: flex;
				justify-content: center;
				align-items: center;
				min-height: 180px;

				img{
					width: 100%;
					max-width: 120px;
					max-height: 120px;
					height: 100%;
					object-fit: cover;
				}
			}

			&:nth-of-type(2){
				padding: 20px;
				display: flex;
				align-items: flex-end;
			}

			&:nth-of-type(3){
				grid-column: 1/3;
				padding: 40px 35px;
			}

			&:nth-of-type(4){
				grid-column: 1/3;
				grid-row: 2/3;

				a{
					width: 100%;
					display: flex;
					justify-content: flex-end;
					padding: 20px 30px;
				}
			}
		}
	}

	#catalogue-filters{
		padding: 70px 40px 55px 40px;
		border-bottom: 1px solid var(--light-gray);
		position: fixed;
		width: 100%;
		background: var(--white);
		left: 0;
		top: 0;
		height: 100vh;
		overflow: auto;
		z-index: 3;
		display: none;

		&.active{
			display: block;
		}
	}

	.catalogue-content__open-filter{
		display: block;
	}

	.catalogue-filters__close{
		display: block;
		position: absolute;
		left: 15px;
		top: 15px;
	}

	.catalogue-content__header{
		padding: 15px 20px;
		display: flex;
		border-bottom: 1px solid var(--light-gray);
	}

	.catalogue-filters__header{
		display: flex;
		justify-content: space-between;
		gap: 20px;
		margin-bottom: 15px;
	}

	.catalogue__filter-group{
		padding: 15px 0;

		h3{
			margin-bottom: 15px;
		}

		&>div{
			margin-bottom: 30px;
		}

		li{
			margin-bottom: 6px;

			&:last-child{
				margin-bottom: 0;
			}
		}

		button{
			margin-top: 10px;
		}

		label:not(.box-label, .color-selection-label){
			padding-left: 40px;
		}

		label:not(.box-label, .color-selection-label),
		label.color-selection-label{

			&::after{
				width: 15px;
				height: 15px;
				top: 3px;
				left: 0;
			}

			&::before{
				width: 7px;
				height: 7px;
				left: 4px;
				top: 7px;
			}
		}

	}

	.filter-color{
		img{
			width: 15px;
			height: 15px;
		}
	}

	.box-selection-container{
		gap: 25px;
	}
	
	.box-label{
		min-width: 55px;
		height: 55px;
		padding: 10px;
	}

	.box-selection-container{
		gap: 15px;
	}

	.accordion-icon{
		width: 15px;
		height: 15px;
	}
	
	.catalogue-content{
		border-left: 1px solid var(--light-gray);
	}

	.catalogue-content__header{
		padding: 15px 20px;
		display: flex;
		border-bottom: 1px solid var(--light-gray);
	}
	
	.catalogue-content__sorting{
		display: flex;
		gap: 10px;
		margin-left: auto;

		select{
			padding-right: 15px;
			border: 0;
			color: var(--gray);
			font: var(--body-2);
			-webkit-appearance: none;
			-moz-appearance: none;
			position: relative;
			background: transparent url("../img/select-arrow.svg") no-repeat right 0px center;
			background-position-y: 8px;
			background-size: 9px 5px;
		}
	}

	.catalogue-content__main{
		display: grid;
		grid-template-columns: 50% 50%;

		&>div{
			border-bottom: 1px solid var(--light-gray);

			&:nth-child(3n+3){
				grid-column: 1/3;
			}

			&:nth-child(3n+1){
				border-right: 1px solid var(--light-gray);
			}
		}
	}

	.catalogue-card-big__main{
		padding: 25px;
		position: relative;

		img{
			width: 110px;
			height: 110px;
			object-fit: cover;
			display: block;
			margin: 0 auto;
		}
	}

	.catalogue-card-big__flash{
		position: absolute;
		right: 10px;
		top: 10px;
	}

	.catalogue-card-big__price{
		position: absolute;
		right: 10px;
		bottom: 10px;
	}

	.catalogue-card-big__info{
		border-top: 1px solid var(--light-gray);

		h2{
			padding: 10px;
			word-break: break-word;
		}

		button{
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			width: 100%;
			height: 100%;

			&:nth-child(1){
				border-right: 1px solid var(--light-gray);
			}
		}

		&>div{
			border-top: 1px solid var(--light-gray);
			display: grid;
			grid-template-columns: 1fr 40px;
			min-height: 40px;
		}
	}

	.catalogue-content__more{
		padding: 20px;
	}

	.brands{
		display: grid;
		grid-template-columns: repeat(12, 8.3333%);

		&>div{
			display: flex;
			justify-content: center;
			align-items: center;
			padding: 20px;
			border-bottom: 1px solid var(--light-gray);
			border-right: 1px solid var(--light-gray);

			img{
				width: 100%;
				height: 100%;
				max-width: 170px;
				max-height: 115px;
				object-fit: contain;
			}

			&:nth-of-type(2n+1){
				border-right: 0;
			}

			&:nth-of-type(1){
				grid-column: 1/6;
				grid-row: 1/3;
				border-right: 1px solid var(--light-gray);

				img{
					max-width: 300px;
					max-height: 200px;
				}
			}

			&:nth-of-type(2),
			&:nth-of-type(3){
				grid-column: 6/13;
				padding: 0;
			}

			&:nth-of-type(2){
				display: flex;
				min-height: 180px;
				padding: 15px 20px;
				align-items: flex-end;
				border-bottom: 1px solid var(--light-gray);
				border-right: 0;
			}

			&:nth-of-type(3){
				padding: 40px;
				grid-column: 1/13;
			}
			
			&:nth-of-type(2n+4){
				grid-column: 1/7;
			}

			&:nth-of-type(2n+5){
				grid-column: 7/13;
			}

			

		}
	}

	.product{
		background: var(--white);
		border-bottom: 1px solid var(--light-gray);
	}

	.product__left{
		padding: 40px 15px;
		position: relative;
	}

	.product__image-container{
		display: grid;
		grid-template-columns: 40px 1fr 40px;
		justify-items: center;
		align-items: center;
	}

	.product-carouser__container{
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	.carousel-btn{
		padding: 15px;

		&.swiper-button-disabled{
			visibility: hidden;
		}
	}

	.carousel-prev{
		transform: rotate(-180deg);
	}

	.product-carousel{
		height: 210px;
		overflow: hidden;

		.swiper-slide{
			width: 40px;
			height: 40px;

			img{
				width: 100%;
				height: 100%;
				object-fit: contain;
			}
		}
	}

	.product__image-main{
		height: 220px;
		width: 220px;

		img{
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	.product__flash{
		position: absolute;
		top: 15px;
		right: 15px;
	}

	.product__brand{
		position: absolute;
		right: 15px;
		bottom: 15px;
		width: 60px;
		height: 60px;
		object-fit: cover;
	}

	.product__right{
		padding: 15px;
		display: flex;
		gap: 40px;
		flex-direction: column-reverse;
		border-top: 1px solid var(--light-gray);
	}

	.product__info{
		width: 100%;

		h1{
			padding-bottom: 10px;
			display: none;
		}

		li{
			display: grid;
			grid-template-columns: 50% 50%;
			gap: 15px;
		}

		.accordion-trigger{
			padding: 15px 0;
			border-top: 1px solid var(--light-gray);	
	
			span{
				display: flex;
				justify-content: space-between;
			}
		}	

		.accordion-panel{
			padding-bottom: 15px;
		}
	}

	.product__checkout{
		display: flex;
		flex-direction: column;
		gap: 10px;
		width: 100%;
	}

	.product__title-mobile{
		display: block;
		padding-bottom: 15px;
		margin-bottom: 5px;
		padding-top: 10px;
		border-bottom: 1px solid var(--light-gray);
	}

	.product__add-to-cart{
		padding: 10px;
	}

	.product__options{
		display: grid;
		grid-template-columns: 40px 1fr;
		min-height: 40px;
		gap: 10px;

		button{
			padding: 7px;
			border: 1px solid var(--light-gray);
			display: block;
			display: flex;
			justify-content: center;
			align-items: center;

			svg{
				display: block;
				width: 15px;
				height: 15px;
			}
		}
	}

	.product__availibility{
		display: flex;
		justify-content: space-between;
		gap: 10px;

		a{
			text-decoration: underline;
		}
	}

	.product-also{
		background: var(--white);
		padding-bottom: 80px;
	}

	.product-also__heading{
		min-height: 100px;
		padding: 20px 25px;
		display: flex;
		align-items: flex-end;
		border-bottom: 1px solid var(--light-gray);
	}

	.product-also__slider{
		overflow: hidden;
		border-bottom: 1px solid var(--light-gray);
		border-left: 1px solid var(--light-gray);

		.swiper-slide{
			border-right: 1px solid var(--light-gray);
		}
	}

	.product-also__slider-footer{
		display: flex;
		align-items: center;
		margin-top: 20px;
	}

	.product-also__controls{
		display: flex;
		margin-left: 5px;
		margin-right: 25px;
	}

	.product-also__button{
		padding: 5px;
		cursor: pointer;

		svg{
			display: block;
			width: 15px;
			height: 15px;
		}

		&.swiper-button-disabled{
			opacity: .5;
			cursor: default;
		}
	}

	.product-also-prev{
		transform: rotate(-180deg);
	}

	.swiper-pagination-progressbar.swiper-pagination-horizontal{
		position: relative;
		height: 1px;
	}

	.swiper-pagination-progressbar .swiper-pagination-progressbar-fill{
		background-color: var(--black);
	}

	.product-reviews{
		border-top: 1px solid var(--white);

		&>div{
			padding: 30px;
			padding-top: 40px;
			border-top: 1px solid var(--light-gray);
		}

		h2{
			padding-bottom: 15px;
		}

		form{
			padding-top: 40px;
			display: flex;
			flex-direction: column;
			border-top: 1px solid var(--light-gray);

			.arrow-button{
				width: 100%;
				display: block;
				padding: 20px 15px;
				border-top: 1px solid var(--gray);
				text-align: right;
				margin-top: auto;

				span:after{
					width: 15px;
					height: 15px;
				}
			}
	
			fieldset{
				display: flex;
				flex-direction: column;
				gap: 30px;
				width: 100%;

				&>label{
					width: 100%;
				}
			}
		}
	}

	.product-review__header{
		display: grid;
		grid-template-columns: min-content 1fr min-content;
		column-gap: 15px;
		align-items: center;
		padding-top: 15px;
		margin-bottom: 15px;

		img{
			width: 40px;
			height: 40px;
			border: 1px solid var(--black);
			border-radius: 50%;
		}

		.product-review__stars{
			display: flex;
			flex-direction: row;
			gap:5px;

			svg{
				width: 15px;
				height: 15px;
			}
		}
	}

	.product-review__content{
		display: flex;
		flex-direction: column;
		align-items: center;

		span{
			flex-shrink: 0;
			display: block;
			margin-left: auto;
		}
	}

	.product-reviews__comments{
		li{
			border-bottom: 1px solid var(--light-gray);

			&:last-child{
				border: 0;
			}
		}
	}

	.raiting__group {
		display: flex;
		gap: 10px;
		margin-top: 15px;

		input { display: none; }

		label {
			width: 15px;
			height: 15px;
			background: url("../img/star.svg");
			background-size: cover;
		}

		input:checked ~ label {
			background-image: url("../img/star-empty.svg");
			
		}

		label.star:hover { transform: rotate(-15deg) scale(1.3); }

		label.star:before {
			background-image: url("../img/star.svg");
		}
	}

	.product-reviews__form-raiting{
		flex-shrink: 0;

		label{
			margin-bottom: 20px;
		}
	}

	.product-reviews__form-container{
		padding: 0 30px 40px 30px;
	}

	.product-delivery__header{

		&>div{
			padding: 40px 30px;

			h2{
				margin-bottom: 15px;
			}

			p{
				max-width: 400px;
			}

			&:nth-child(1){
				border-bottom: 1px solid var(--white);
			}
		}
	}

	.product-delivery__map{
		position: relative;
		overflow: hidden;
		border-top: 1px solid var(--light-gray);

		iframe{
			width: 100%;
			min-height: 570px;
			border: 0;
		}
	}
}